@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-details{
	background:#fff;

	.detail-top{
		padding:2% 3.2%;
		border-bottom:1px solid @border-color-base;
		overflow: hidden;

		.img{
			border-radius: 50%;
		}

		.name{
			color:@color666;
			padding-left: 10px;
		}

		.date{
			color: @color999;
			display: flex;
		    align-items: center;
		    justify-content: flex-end;

			&:before{
				.nmq-icon();
				content:"\e604";
				font-size: @font-size-lg;
				padding-right:3px;
			}

			.icon-date{
				font-size: @font-size-lg;
				vertical-align: top;
			}
		}
	}

	.detail-content{
		padding: 10px 3.2% 0;
		overflow: hidden;

		.mod-tlt{
			padding-bottom:5px;
			font-size:0;
			.mark{
				font-size: @font-size-sm;
				color:#fff;
				border-radius: 3px;
				margin-right:5px;
				padding:1px 3px;
				.relative(-2px,_,_,_);

				&-b{
					background: #1ba0d1;
				}
				&-y{
					background: #eb6710;
				}
				&-r{
					background: @price-color;
				}
			}
			span{
				font-weight: bold;
				font-size: @font-size-lg;
				line-height:1.6;
				padding: 5px 0;
			}
		}

		.source{
			color: @color666;
			padding: 5px 0;
			font-size: @font-size-lg;

			em{
				color: @nmq-theme-color;
			}
		}

		.content{
			line-height: 1.8;
			text-indent: 2em;
			font-size: @font-size-lg;
		}

		.content-img{
			margin: 10px 0;

			&:last-child{
				margin-bottom:0;
			}

			img{
				max-width: 100%;
				vertical-align: top;
			}
		}

		.img-box{
			&:before{
				padding-top: 57%;
			}
		}


	}
}
