@import "../variables";
@import "./mask";


.@{css-prefix}dialog {
	.fixed(50%, _, _, 50%);
	transform: translate(-50%, -50%);
	z-index: @zindex-dialog;
	background-color: #fff;
	text-align: center;
	border-radius: 4px;
	width: 85%;
	max-height: 80%;
	overflow-x: hidden;
	overflow-y: auto;
	font-size: @font-size-lg;
	box-sizing: border-box;
    font-family: arial,'Microsoft Yahei';

    *{
		box-sizing: border-box;
	}
}

.@{css-prefix}dialog-hd {
	padding: 14px 0 8px;
	font-size: @price-size-num-lg;
}

.@{css-prefix}dialog-title {
    font-weight: 400;
    font-size: @price-size-num-lg;
}

/* .@{css-prefix}dialog-bd {
	text-align: left;
} */

.@{css-prefix}dialog-content{
	padding: 40px 10px;
}

.@{css-prefix}dialog-ft {
	border-radius: 0 0 4px 4px;
	overflow: hidden;
	border-top: 1px solid @border-color-base;
}

.@{css-prefix}dialog-ft-button{
	border-left: 1px solid @border-color-base;
	padding: 10px 0;
	color: #007AFF;

	&:active{
		background: #fafafa;
	}

	&:first-child{
		border-left: none;
	}
}

@media (min-width: @screen-lg-min) {
	.@{css-prefix}dialog {
        width: 40%;
    }
}

