@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-comment{
	 background: #fff;

	.comment-num{
		background: @nmq-theme-color;
		padding: 5px 10px;
		display: inline-block;
		color: #fff;
	}

	.item{
		margin-left: 3.2%;
		padding: 3.2%  3.2% 3.2% 0;
		border-bottom: 1px solid @border-color-base;
		overflow: hidden;

		.item-img{
			display: block;
			.size(50px);
			float: left;

			.img{
				border-radius: 50%;
			}
		}

		.comment-right{
			padding-left: 60px;

			.name{
				color: @color999;
				padding:15px 0 0 0;
				line-height: 1.6;

				.master{
					border: 1px solid #d6005b;
					color: #d6005b;
					border-radius: 3px;
					margin-left: 5px;
					font-size: @font-size-sm;
					padding:.03rem .05rem;
				}

				.floor{
					float: right;
					font-size:@font-size-sm;
				}
			}

			.detail{
				padding-top:10px;
				font-size: @font-size-lg;
			}
			.pic-container{
				padding-top:10px;
				img{
					max-width:100%;
					vertical-align: top;
				}
			}

			.date{
				padding-top: 10px;
				color: @color999;
				font-size: @font-size-base;
				overflow: hidden;

				.date-left{
					float: left;
					display: flex;
		    		align-items: center;

					&:before{
						.nmq-icon();
						content:"\e604";
						font-size: @font-size-lg;
						padding-right:3px;
					}

					.icon-time{
						font-size: @font-size-lg;
						vertical-align: top;
					}
				}

				.date-right{
					float: right;
					color: @color999;

					.icon-zan{
						color: @fu-color;
					}

					.active{
						color: @nmq-theme-color;
					}
				}
			}

			.reply{
				background: @container-bg-color;
				border-radius: 5px;
				margin: 10px 0 0;
				padding: 10px;

				.reply-name{
					color: @color999;
					padding-bottom: 5px;

					em{
						padding-right: 10px;
					}
				}
			}
		}

		&:last-child{
			border-bottom: 0;
		}
	}

	.fu-color{
		color: @fu-color;
	}
}
