@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-rb-nav{

	.rb-nav{
		position: fixed;
    	right: 20px;
    	bottom: 58px;
    	z-index: 200;
	}
	
	.icon-edit{
		@size:50;
		
		display: block;
		.size(unit(@size,px));
		border-radius: unit(@size,px);
		background: rgba(255,140,163,0.8);
		color:#fff;
		font-size:28px;
		text-align: center;
		line-height:unit(@size,px);
	}
}