@import "../_nmq-variables";
@import "../_nmq-mixins";

.nmq-choose-dialog(@index){
	@index:@zindex-nav;
	.nav-mask-layer{
		.fixed(0,0,0,0);
		background-color: rgba(0, 0, 0, 0.6);
		opacity: 0;
		z-index: -1;
		transition:opacity 0.3s ease;
		display: none;
	}
	.nav-content{
		.fixed(_,0,0,0);
		width: 100%;
		max-width: unit(@container-max-width, px);
		margin: 0 auto;
		height: 90%;
		min-height: 240px;
		z-index: -1;
		transform: translateY(100%);
		transition: transform 0.3s ease;
		padding-top: 40px;

		.panels{
			height:100%;
		}
	}
	.nav-close{
		.absolute(0,10px,_,_);
		.size(50px, 40px);
		background: #fff;
		border-top-right-radius: 6px;
		border-top-left-radius: 6px;

		&:before,&:after{
			content: '';
			.absolute(50%,_,_,20%);
			.size(60%, 5%);
			background-color: #6c6c6c;
			transform-origin: 50% 50%;
		}

		&:before{
			transform: rotate(45deg);
		}

		&:after{
			transform: rotate(-45deg);
		}
	}
	&.active{

		.nav-mask-layer{
			z-index: @index;
			opacity: 0.7;
			display: block;
		}

		.nav-content{
			transform: translateY(0);
			z-index: @index;

			&-inner{
				padding: 4px 4px 0 0;
				background: #fff;
				height: 100%;
				position: relative;
			}
		}

	}
}
