@import "./_nmq-variables.less";
@import "./_nmq-mixins.less";
@import "../common/widget/dialog";

.iconfont-face(@nmq-icon-name, @nmq-icon-path);

.@{nmq-icon-name} {
	.iconfont-mixin(@nmq-icon-name);
}

.@{container-class}{
	background: @container-bg-color;

	.mt2{
		margin-top: 2%;
	}

	i, em{
		font-style: normal;
	}
	.prolist-title{
		line-height:50px;
		color: #999;
		text-align: center;
		position: relative;


		&:before{
			content:"";
			height:1px;
			.absolute(50%,_,_,50%);
		}
		span{
			vertical-align: top;
			display: inline-block;
			padding: 0 4%;
			background: @container-bg-color;
			position: relative;
		}

	}
	.prolist-title:before{
		width:80%;
		margin-left: -40%;
		background: #e1e1e1;
	}

}

@import './module/fn-nmq-common-hd.less';
@import './module/fn-nmq-rb-nav.less';
@import '../common/widget/dialog.less';
@import './module/fn-nmq-loading-btn.less';


