@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-detail-bottom{
	width: 100%;
	max-width: @container-max-width;
	background: #fff;
	border-top: 1px solid @border-color-base;
	overflow: hidden;
	position: fixed;
	bottom: 0;

	.detail-bottom{
		width: 50%;
		display: block;
		padding: 16px 10px;
		float: left;
		text-align: center;
		border-right:1px solid @border-color-base;
		.ellipsis();

		&:last-child{
			border-right: 0;
		}

		.bottom-icon{
			color: @nmq-theme-color;
			font-size: 20px;
			vertical-align: middle;
		}

		.num{
			color: @color999;
		}
	}
}
