@import "../variables";

.@{css-prefix}mask, .@{css-prefix}mask-transparent, .@{css-prefix}mask-transition {
	.fixed(0,_,_,0);
	.size(100%);
	z-index: @zindex-mask;
}

.@{css-prefix}mask {
	background: rgba(0, 0, 0, .6);
}

.@{css-prefix}mask-transition {
	display: none;
	background: rgba(0,0,0,0);
	transition: background .3s;
}

.@{css-prefix}mask-transition-fade{
	background: rgba(0,0,0,.6);
}
