@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-reply-box{

	.rule-reply{

		.reply-wrap{
			.fixed(_,_,0,0);
		   	z-index: 301;
		    background: @border-color-base;
		    width: 100%;
		    padding: 3.2%;

		   .reply-top{
		   		padding-bottom: 3%;

		   		.close{
					padding-right: 10%;
					font-size: .64rem;
					color: @color999;
		   		}

		   		.pic{
		   			.size(20px,16px);
		   			display: inline-block;
					background: data-uri('@{nmq-image-path}/release-bottom-pic01.png') no-repeat;
					background-size: 100% 100%;
					color: @color999;
					margin: 3px 5px 0 0;

		   		}

		   		.show-pic{
		   			.size(20px);
		   			display: inline-block;
		   			margin: .10rem .28rem 0 0;
		   			position: relative;

		   			.pic-close{
		   				.absolute(-8px,-8px,_,_);
		   				color: @nmq-theme-color;
		   			}
				}

		   		.btn{
		   			float: right;
		   			padding: 3px 10px;
		   			background: #f6bcc8;
		   			color: #fff;
		   			display: inline-block;
		   			border-radius: 3px;

		   			&.active{
		   				background: @nmq-theme-color;

		   			}
		   		}
		   }

		   .details{
		   	width: 100%;
		   	border: 1px solid #d5d5d5;
		   	border-radius: .08rem;
		   	background: #fff;
		   	min-height: 80px;
		   	padding: .08rem;
		   	line-height: 1.3;
		   	color: #666;
		   }
		}
	}
}

