@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-commodity-list{
	background: #fff;

	.list-wrap{
		padding: 0 3.2% 3.2%;
		overflow:hidden;

		.item{
			background: @container-bg-color;
			padding: 10px;
			overflow: hidden;
			border-radius: 5px;
			margin-top: 3.2%;

			.img-box{
				border: 1px solid @border-color-base;
			}

			.info{
				padding-left: 10px;

				.tlt{
					.ellipsis(2);
					padding-bottom: 5px;
				}
			}
		}

		.switch-item-btn{
			@color: #83dcde;

			padding: 20px 0;

			.icon{
				.size(42px, 100%);
				position: relative;
				display:table-cell;
				transition: transform .3s ease;

				&:before,&:after{
					content: '';
					.absolute(50%,_,_,_);
					background: @color;
					.size(30%, 5%);
					transform-origin: 50% 50%;
				}

				&:before{
					left: 25%;
					transform: rotate(45deg);
				}

				&:after{
					right: 24.5%;
					transform: rotate(-45deg);
				}
			}

			.down{
				display:table;
				padding: 5px 5px 5px 20px;
				margin:0 auto;
				border-radius:50px;
				border: 1px solid @color;
				color: @color;
			}

			.up{
				display:none;
				padding: 5px 40px;
				margin:0 auto;
				border-radius:50px;
				border: 1px solid @color;
				color: @color;

				.icon{
					transform: rotate(180deg);
				}
			}
		}

		.item-toggle{
			display: none;
		}

		/* .switch-item-btn{
			.down{
				display: table;
			}

			.up{
				display: none;
			}
		} */

		&.active{
			.item-toggle{
				display: block;
			}

			.switch-item-btn{
				.down{
					display: none;
				}

				.up{
					display: table;
				}
			}
		}

		.report{

			.icon-report{
				font-size: .88rem;
				color:#bbb;
				
			}

			.num{
				color: @color999;
				padding-left: 5px;
			}

			.report-right{
				color: @color666;
				float: right;
			}
		}
	}
}
